import React, { useState, useEffect } from "react";
import "./BoxHistory.css";

const texts = [
  "En la dcada de 1950, un equipo lucha con pasin y pocos recursos en carreras improvisadas, ganando seguidores leales.",
  "Durante los aos 1960, el equipo asciende con el respaldo de un patrocinador hacia victorias ms prestigiosas.",
  "En los aos 1970, el equipo crece rpidamente, atrayendo patrocinadores y tecnologa de vanguardia para dominar las pistas.",
  "En los aos 1980, enfrentan conflictos internos y financieros, pero se recuperan con innovacin y unidad hacia el xito renovado.",
  "Para los aos 1990, el equipo se consolida como un nombre conocido, manteniendo sus valores fundamentales mientras dominan en la pista.",
  "En el cambio de milenio, el equipo se adapta y lidera la evolucin del automovilismo, enfrentando competencia y desafos tecnolgicos.",
  "Durante los aos 2010, el equipo innova en sostenibilidad y diversidad, manteniendo su posicin de liderazgo en competiciones globales.",
  "A principios de la dcada de 2020, la pandemia presenta desafos, pero el equipo muestra resiliencia y determinacin para seguir adelante.",
];

export default function BoxHistory({ decada }) {
  const [texto, cambiarTexto] = useState("");

  useEffect(() => {
    // Cambiar el texto segn la dcada
    switch (decada) {
      case "50":
        cambiarTexto(texts[0]);
        break;
      case "60":
        cambiarTexto(texts[1]);
        break;
      case "70":
        cambiarTexto(texts[2]);
        break;
      case "80":
        cambiarTexto(texts[3]);
        break;
      case "90":
        cambiarTexto(texts[4]);
        break;
      case "00":
        cambiarTexto(texts[5]);
        break;
      case "10":
        cambiarTexto(texts[5]);
        break;
      case "20":
        cambiarTexto(texts[6]);
        break;
      default:
        cambiarTexto("Error 404"); // Por si hay un valor de dcada no reconocido
    }
  }, [decada]);

  return (
    <>
      <div className="col-12 d-flex justify-content-center">
        <div className="col-10 text-light text-center">
          <p id="decada">{texto}</p>
        </div>
      </div>
    </>
  );
}
